import { Component, OnInit , ViewChild} from '@angular/core';
import { MenuController, IonInfiniteScroll, IonContent } from '@ionic/angular';
import { Router } from '@angular/router';



@Component({
  selector: 'app-ranking',
  templateUrl: './ranking.page.html',
  styleUrls: ['./ranking.page.scss'],
})
export class RankingPage implements OnInit {

  items = [];
  data =[];
  counter=0;

  @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;
  @ViewChild('pageTop') pageTop: IonContent;
  
  constructor(private menu: MenuController,private router:Router) {

    this.fillData();
    this.addFirst();

   }

  ngOnInit() {
  }

  openEnd() {
    this.pageTop.scrollToTop();
    this.menu.close('start');
    this.menu.enable(true, 'start');
    this.menu.open('start');
    this.menu.close('start');
  }

  goToRanking(){
    this.router.navigate(['/ranking']);
  }

  goToSettings(){
    this.router.navigate(['/connect']);
  }

  goToMenu(){
    this.router.navigate(['/menu']);
  }

  goOut(){
    this.router.navigate(['/home']);
  }


fillData(){

  for (let i= 1; i <= 100; i++) {
       

    this.data.push(i);
    
  }
}
  
  loadData(event) {
  
    setTimeout(() => {
      console.log('Done');
      event.target.complete();
      console.log('Done1 :'+this.items.length);
      console.log('Done2 :'+this.data.length);
      if (this.items.length == this.data.length) {
        event.target.disabled = true;
      }else{
        this.addMoreItems();
      }

    }, 1000);
  }

  addMoreItems(){
    if(this.items.length+10 > this.data.length){
      
      for (let i= this.items.length+1; i <= this.data.length; i++) {
     
       
        this.items.push(this.data[this.counter]);
        this.counter++;
      }

    }else{


      for (let i= 1; i <= 10; i++) {
     
       
        this.items.push(this.data[this.counter]);
        this.counter++;
       
      }


    }


  }

    addFirst(){
      for (let i=0 ; i < 25; i++) {
       
        this.items.push(this.data[this.counter]);
        this.counter++;
      }
  }


  refresh(){
    this.items = [];
    this.data =[];
    this.counter=0;
    this.fillData();
    this.addFirst();
  }
}
